class Collapsible extends React.Component {
	state = {
		isExpanded: false
	}

	handleToggle(e){
		e.preventDefault();
		console.log(this.refs.inner.clientHeight);
		this.setState({
			isExpanded: !this.state.isExpanded,
			height: this.refs.inner.clientHeight
		})
	}


	render(){
		const {title, children} = this.props;
		const {isExpanded, height} = this.state;
		const currenHeight = isExpanded ? height : 0;

		return(
			<div className='box-Collapsible'>
				<Container>
					<Col lg='6' className='mx-auto'>
						<Card>
							<CardBody>
								<div className="panel" onClick={(e) => (this.handleToggle(e))}>
									<p> {title} </p>
								</div>
								<div className='panel-collapse' style={{height: currenHeight+'px'}}>
									<div className="box-panel" ref="inner">
										 {children} 
									</div>
								</div>
							</CardBody>
						</Card>
					</Col>
				</Container>
			</div>
		);
	}
}


Collapsible.PropTypes = {
	title: PropTypes.string
}
