import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import { App } from './App';
import { Dashboard } from './Components/Dashboard';
import { TableInput } from './Components/TableInput';
import { CollapsibleComponent } from './Components/CollapsibleComponent';
import { CollapsibleType2 } from './Components/CollapsibleType2';


import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faEnvelope, faUserCircle, faTachometerAlt, faFileSignature, faFileInvoice, faTable, faCogs, faCircleNotch, faAngleRight, faAngleDown, faTimes } from '@fortawesome/free-solid-svg-icons'

library.add(faBars, faEnvelope, faUserCircle, faTachometerAlt, faFileSignature, faFileInvoice, faTable, faCogs, faCircleNotch, faAngleRight, faAngleDown, faTimes)

export class Router extends React.Component{
	render(){
		return(
			<BrowserRouter>
				<Switch>
					<Route path='/' component={Dashboard} exact />
					<Route path='/tableinput' component={TableInput} />
					<Route path='/collapsiblecomponent' component={CollapsibleComponent} />
					<Route path='/collapsibletype2' component={CollapsibleType2} />
				</Switch>
			</BrowserRouter>
		)
	}
}