import React from 'react';
import { Sidebar } from './../Template/Sidebar';
import { AdminNavbar } from './../Template/AdminNavbar';
import { Footer } from './../Template/Footer';
import { BarChart, LineChart, DoghnutChart, HorizontalChart } from './../Template/ChartJs';
import PropTypes from 'prop-types';
import { Container, Row, Col, Card, CardBody, CardTitle, CardText, CardHeader,  Breadcrumb, BreadcrumbItem } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

export class Dashboard extends React.Component{
	render(){
		return(
			<section className='AdminMaster'>
				<Sidebar />
				<AdminNavbar />
				<div className='AdminMaster-Content'>
					<div className="Dashboard">
						<Container fluid>
							<Breadcrumb>
						        <BreadcrumbItem active>Dashboard</BreadcrumbItem>
						    </Breadcrumb>
							<Row>	
								<CollapsibleBox title="Bar Chart"> 
									<BarChart />
								</CollapsibleBox>
								<CollapsibleBox title="Line Chart"> 
									<LineChart />
								</CollapsibleBox>
								<CollapsibleBox title="Doghnut Chart"> 
									<DoghnutChart />
								</CollapsibleBox>
								<CollapsibleBox title="Horizontal Chart"> 
									<HorizontalChart />
								</CollapsibleBox>
							</Row>
						</Container>
					</div>
				</div>
				<div className='clearFix'> </div>
				<Footer />
			</section>
		)
	}
}

class CollapsibleBox extends React.Component{
	state = {
		isExpanded: false,
		arrowBox: "angle-right",
		displayBox: "block"
	}

	handleToggle(e){
		console.log(this.refs.inner.clientHeight)
		this.setState({
			isExpanded: !this.state.isExpanded,
			height: this.refs.inner.clientHeight
		})
	}

	handleClose(e){
		this.setState({
			displayBox: "none"
		})
	}

	render(){
		const { isExpanded, height, arrowBox, displayBox } = this.state;
		const { title, children } = this.props;
		const currentHeight = isExpanded ? 0 : height;
		const currentArrow = isExpanded ? arrowBox : "angle-down";
		const currentDisplay = displayBox;
		return(
			<Col lg='6'>
				<Card className='mb-3' style={{display: currentDisplay}}>
					<CardHeader> 
						{title} 
						<FontAwesomeIcon icon={currentArrow} onClick={(e) => (this.handleToggle(e))} />
						<FontAwesomeIcon icon="times" onClick={(e) => (this.handleClose(e))} />
					</CardHeader>
					<CardBody>
						<div className='panel-collapse' style={{height: currentHeight+'px'}}>
							<div ref="inner">
								{children}
							</div>
						</div>					
					</CardBody>
				</Card>
			</Col>
		)
	}
}

