import React from 'react';
import { Sidebar } from './../Template/Sidebar';
import { AdminNavbar } from './../Template/AdminNavbar';
import { Footer } from './../Template/Footer';
import { Container, Row, Col, Card, CardBody, Table, FormGroup, Label, Button } from 'reactstrap';

let fakeServer = {
	school : {
		user: 'sandy',
		goods : [
			{
				id: 1,
				name: 'Pen',
				description: 'Black',
				total: 20
			},
			{
				id: 2,
				name: 'Pencil',
				description: 'Blue',
				total: 10
			},
			{
				id: 3,
				name: 'Eraser',
				description: 'White',
				total: 16
			},
			{
				id: 4,
				name: 'Bag',
				description: 'Convers',
				total: 90
			},
			{
				id: 5,
				name: 'Shoes',
				description: 'Old School',
				total: 100
			}
		]
	}
}

export class TableInput extends React.Component{
	state = {
		fakeData : {},
		defaultData: ['Pagani', 'Maserati', 'Bugati'],
		message: ''
	}


	componentDidMount(){
		this.setState({
			fakeData: fakeServer
		})
	}

	addData(e){
		e.preventDefault();
		const {defaultData} = this.state;
		const nameCar = this.nameCar.value;
		const checkData = this.state.defaultData.includes(nameCar);

		if(checkData){
			this.setState({
				message: "data already exist"
			})
		}
		else{
			nameCar != '' && this.setState({
				defaultData: [...this.state.defaultData, nameCar],
				message: ''
			})
		}
	}

	removeData(data){
		const afterRemoveData = this.state.defaultData.filter(defaultData => {
			return defaultData !== data;
		})

		this.setState({
			defaultData: [...afterRemoveData],
		})
	}

	removeAll(e){
		this.setState({
			defaultData: [],
			message: "No data"
		})
	}

	render(){
		let number = 1;

		return(
			<section className='AdminMaster'>
				<Sidebar />
				<AdminNavbar />
				<div className='AdminMaster-Content'>
					<div className="TableInput">
						<Container fluid>
							<Row>
								<Col lg='12'>
									<Card className='mb-3'>
										<CardBody>
											<Table responsive hover bordered size="sm">
												<thead>
													<tr>
														<th> No </th>
														<th> Car </th>
														<th> Action </th>
													</tr>
												</thead>
												<tbody>
													{this.state.defaultData.map((data, index) =>{
														return(
															<tr key={index}>
																<td> {number++} </td>
																<td> {data} </td>
																<td>
																	<Button outline color="danger" onClick={(e) => (this.removeData(data))}>
																		Remove
																	</Button>
																</td>
															</tr>
															)
													})}
													{this.state.defaultData.length === 0 && this.state.message}
												</tbody>
											</Table>	
											{this.state.message != '' && this.state.message}
											<Button onClick={(e) => (this.removeAll(e))}> Remove all data </Button>
											<br />
											<Row>
												<Col lg='8' className='mx-auto'>
													<form className='form' onSubmit={(e) => (this.addData(e))}>
														<FormGroup row>
												          <Label for="exampleEmail" sm={3}>Goods</Label>
												          <Col sm={9}>
												            <input className='form-control' type="teks" name="teks" ref={(input) => (this.nameCar) = input}/>
												          </Col>
												        </FormGroup>
												        <FormGroup row>
												          <Col sm={6} className='mx-auto'>
												            <Button outline color="primary"> Add </Button>
												          </Col>
												        </FormGroup>
													</form>
												</Col>
											</Row>
										</CardBody>
									</Card>
								</Col>
							</Row>
						</Container>
					</div>
				</div>
				<div className='clearFix'> </div>
				<Footer />
			</section>
		)
	}
}