class ToggleSlider extends React.Component{
	Slider(e){
		e.preventDefault();
		const AdminMaster = document.getElementsByClassName('AdminMaster-Content')[0];
		const Sidebar = document.getElementsByClassName('Sidebar')[0];
		const Navbar = document.getElementsByClassName('AdminNavbar')[0];
		const Footer = document.getElementsByTagName('footer')[0];
		console.log(Footer)
		Sidebar.classList.toggle('SidebarToggle');
		Navbar.classList.toggle('AdminNavbarToggle');
		AdminMaster.classList.toggle('AdminMaster-Content-Toggle');
		Footer.classList.toggle('footerToggle');
	}
	render(){
		return(
			<NavbarBrand href="/" className='ToggleSlider' onClick={this.Slider}>
				<FontAwesomeIcon icon="bars" />
			</NavbarBrand>
		)
	}
}

