class Dropdown extends React.Component{
	state = {
		isExpanded: false,
		arrowDropdown: "angle-down"
	}

	handleToggle(e){
		console.log(this.refs.inner.clientHeight)
		this.setState({
			isExpanded: !this.state.isExpanded,
			height: this.refs.inner.clientHeight
		})
	}

	render(){
		const {title, children} = this.props;
		const {isExpanded, height, arrowDropdown} = this.state;
		const currentHeight = isExpanded ? height : 0;
		const arrow = isExpanded ? arrowDropdown : "angle-right";
		return(
			<div className='Dropdown'>
				<li onClick={(e) => (this.handleToggle(e))}>
					<FontAwesomeIcon icon="cogs" />
					<Link to='#' className='btnDropdown'> 
						{title} 
					</Link>
					<FontAwesomeIcon icon={arrow} className='arrowDropdown' />
				</li>
				<div className="Menu-Dropdown" style={{height: currentHeight+'px'}}>
					<div className="Menu" ref="inner">
						{children}
					</div>
				</div>
			</div>
		)
	}
}

Dropdown.PropTypes = {
	title: PropTypes.string
}