import React from 'react';
import { Container, Col, Row, Card, CardBody, CardHeader, CardText } from 'reactstrap';
import PropTypes from 'prop-types';

export class CollapsibleType2 extends React.Component{
	render(){
		return(
			<div className='CollapsibleType2'>
				<Collapsible title="Collapsible1"> 
					<CardText> Lorem Ipsum is simply dummy text of the printing and typesetting industry</CardText>
				</Collapsible>
				<Collapsible title="Collapsible1"> 
					<CardText> Lorem Ipsum is simply dummy text of the printing and typesetting industry, Lorem Ipsum is simply dummy text of the printing and typesetting industry</CardText>
				</Collapsible>
				<Collapsible title="Collapsible1"> 
					<CardText> Lorem Ipsum is simply dummy text of the printing and typesetting industry, Lorem Ipsum is simply dummy text of the printing and typesetting industry</CardText>
				</Collapsible>
			</div>
		)
	}
}

class Collapsible extends React.Component{
	state = {
		isExpanded: false
	}

	handleToggle(e){
		console.log(this.refs.inner.clientHeight)
		this.setState({
			isExpanded: !this.state.isExpanded,
			height: this.refs.inner.clientHeight
		})
	}

	render(){
		const { title, children } = this.props;
		const { isExpanded, height } = this.state;
		const currentHeight = isExpanded ? 0 : height;
		return(
			<Container>
				<Row>
					<Col lg='4' className='mx-auto'>
						<Card>
							<CardHeader onClick={(e) => (this.handleToggle(e))}>
								{title}
							</CardHeader>
							<CardBody>
							<div className='panel-collapse' style={{height: currentHeight+'px'}}>
								
									<div ref="inner">
										{children}
									</div>
								
							</div>
							</CardBody>
						</Card>
					</Col>
				</Row>
			</Container>
		)
	}
}

Collapsible.PropTypes = {
	title: PropTypes.string
}
